import React from "react";
import "./App.css";
import Home from "./components/home-component/Home";
import Greeting from "./components/greeting-component/Greeting";
import "./components/project-component/Project-Style.css";
import projectList from "./utilities/projectList.js";
import Project from "./components/project-component/Project";

function App() {
  let projects = projectList.map(proj => <Project project={proj}></Project>);
  return (
    <div className="body">
      {/* <Home></Home> */}
      <section className="section">
        <Greeting></Greeting>
      </section>
      <section className="projects-section">
        <div className="container white-bg">
          <h2 className="section-title">Projects</h2>
          {projects}
        </div>
      </section>
      <section className="section"></section>
    </div>
  );
}

export default App;
