import React from "react";
import me from "../../images/christian-2.jpg";
import "./Greeting-Style.css";

export default function Greeting() {
  return (
    <div className="greeting-section">
      <div className="greeting-left">
        <img className="greeting-image" src={me} alt="me!" />
      </div>
      <div className="greeting-right">
        <h2 className="heading-2">Christian&nbsp;de&nbsp;Guzman</h2>
        <h1 className="greeting-title">Software&nbsp;Developer</h1>
        <div className="icons">
          <a className="icon" href="https://www.linkedin.com/in/cdguzman/">
            <i className="fa fa-linkedin"></i>
          </a>
          <a className="icon" href="https://github.com/christiandguzman">
            <i className="fa fa-github"></i>
          </a>
        </div>
      </div>
    </div>
  );
}
